.header {
  z-index: 3;
  display: flex;
  align-items: center;
  flex: 0 0 auto;
  height: $headerHeight;
  background-color: var(--pageHeaderBackgroundColor);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color: var(--white);
}

.logoContainer {
  display: flex;
  align-items: center;
  flex: 0 0 $sidebarWidth;
  padding-left: 20px;
}

.appTitle {
  display: none;
  color: #555;
  font-size: 30px;
}

.logoFull,
.logo {
  vertical-align: middle;
}

.logoFull {
  margin-left: 15px;
  width: 240px;
  height: 40px;
}

.logo {
  width: 40px;
  height: 40px;
}

.sidebarToggleContainer {
  display: none;
  justify-content: center;
  flex: 0 0 45px;
  margin-right: 14px;
}

.right {
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
}

.actionMenu {
  &:hover {
    color: #515253;
  }
}

.translate {
  composes: link from '~Components/Link/Link.css';

  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  color: #fff;
  text-align: center;
  line-height: 60px;

  &:hover {
    color: #515253;
  }
}

.donate {
  composes: link from '~Components/Link/Link.css';

  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  color: var(--themeRed);
  text-align: center;
  line-height: 60px;

  &:hover {
    color: #9c1f30;
  }
}

@media only screen and (max-width: $breakpointSmall) {
  .logoContainer {
    flex: 0 0 60px;
  }

  .sidebarToggleContainer {
    display: flex;
  }
}

@media only screen and (max-width: $breakpointExtraSmall) {
  .donate,
  .translate {
    display: none;
  }
}
