.customFormat {
  composes: card from '~Components/Card.css';

  width: 300px;
}

.nameContainer {
  display: flex;
  justify-content: space-between;
}

.name {
  @add-mixin truncate;

  margin-bottom: 5px;
  font-weight: 300;
  font-size: 20px;
}

.labels {
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;
  pointer-events: all;
}

.tooltipLabel {
  composes: label from '~Components/Label.css';

  margin: 0;
  border: none;
}
